    isNumeric(value: any){
        return !isNaN(value -parseFloat(value));
    }
    checkAbbreviations(indexArray: number[], text: string){
        var spaceIndex: number;
        var returnIndexes: number[] = [];
        var spaceIndexArray: number[] = [];

        for(var i = 0; i < text.length; i++){
            if(text.charAt(i) === " "){
                spaceIndex = i;
            }
            if(text.charAt(i) === "." || text.charAt(i) === "?" || text.charAt(i) === "!"){

                //console.log("ABR fn: char kohal i on -> ", text.charAt(i));
                console.log("ABR fn: eelmisest tühikust kuni punktini -> ",
                text.substring(spaceIndex, i+1));

                var memoryCheckName: boolean =
                this.checkMemoryNameAb(text.substring(spaceIndex, i+1).toLowerCase());
                var memoryCheckEnd: boolean =
                this.checkMemoryOther(text.substring(spaceIndex, i+1).toLowerCase(), text);
                //console.log("ABR fn: nameCheckFn result -> ", memoryCheckName);

                if(!memoryCheckName && !memoryCheckEnd){
                    console.log(i);
                    returnIndexes.push(i);
                }
            }
        }
        console.log("ABR fn: tagastatavad indexid -> ", returnIndexes);
        return returnIndexes;
    }
